import * as Promise from 'bluebird';
import * as unirest from 'unirest';

export class RestService {
  get(url: string): Promise<any> {
    return new Promise(function(resolve, reject) {
       unirest.get(url)
        .headers({'Accept': 'application/json'})
        .send()
        .end(function(resp) {
          if (resp && resp.body) {
            let body;

            if (typeof resp.body === 'string') {
              body = JSON.parse(resp.body);
            } else {
              body = resp.body;
            }

            if (body.success !== undefined && body.success === true) {
              body.response = body.response || {};
              resolve(body.response);
            } else {
              reject(new Error(body.message || 'The response contains no message.'));
            }
          } else {
            reject(new Error('The response contains no body.'));
          }
        });
    });
  }

  getProtected(url: string, authorization: string): Promise<any> {
    return new Promise(function(resolve, reject) {
      unirest.get(url)
      .headers({'Accept': 'application/json', 'Authorization': authorization})
      .send()
      .end(function(resp) {

        if (resp && resp.error) {
          reject(resp.error);
          return;
        }

        if (resp && resp.body) {
          if (typeof resp.body === 'string') {
            resolve(JSON.parse(resp.body));
          } else {
            resolve(resp.body);
          }
        } else {
          reject('no response');
        }
      });
    });
  }

  post(url: string, authorization: string, obj: any): Promise<any> {
    return new Promise(function(resolve, reject) {
      unirest.post(url)
      .headers({'Accept': 'application/json', 'Content-Type': 'application/json', 'Authorization': authorization})
      .send(obj)
      .end(function(resp) {

        if (resp && resp.error) {
          reject(resp.error);
          return;
        }

        if (resp && resp.body) {
          if (typeof resp.body === 'string') {
            resolve(JSON.parse(resp.body));
          } else {
            resolve(resp.body);
          }
        } else {
          reject('no response');
        }
      });
    });
  }
}



