import * as Promise from 'bluebird';
import * as unirest from 'unirest';

import { Injectable } from '@angular/core';

import { environment } from '../../../environments/environment';
import { TrendingClips } from '../models/trending-clips';
import { LocalStorageService } from './localStorage.service';
import { RestService } from './rest.service';

interface Clip {}

@Injectable()
export class ClipsService {
  constructor(
    private localStorageService: LocalStorageService,
    private restService: RestService) {}

    addComment(slug: string, comment: string): Promise<any> {
      let that = this;

      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/${slug}/comments`;

      return this.restService.post(url, authorization, JSON.stringify({"comment": comment}));
    }

    getComments(slug: string): Promise<any> {
      let url = environment.apiAddress + `/api/v1/clips/${slug}/comments`;

      return this.restService.get(url);
    }

    getPublicInfo(slug: string): Promise<any> {
      let url = environment.apiAddress + `/api/v1/clips/${slug}/info`;

      return this.restService.get(url);
    }

    getBySlug(slug: string): Promise<any> {
      let that = this;
      let url = environment.apiAddress + '/api/v1/clips/slug/' + slug;

      return this.restService.get(url);
    }

    getFavorites(): Promise<any> {
      let that = this;

      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + '/api/v1/protected/clips/favorites';

      return new Promise(function(resolve, reject) {
        unirest.get(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {

          if (resp && resp.error) {
            reject(resp.error);
            return;
          }

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
      });
    }

    getUserInfo(slug: string): Promise<any> {
      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/userinfo`;

      return new Promise(function(resolve, reject) {
        unirest.get(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {

          if (resp && resp.error) {
            reject(resp.error);
            return;
          }

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
      });
    }

    addFavorite(slug: string): Promise<any> {
      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/favorite`;

      return new Promise(function(resolve, reject) {
        unirest.post(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {

          if (resp && resp.error) {
            reject(resp.error);
            return;
          }

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
      });
    }

    removeFavorite(slug: string): Promise<any> {
      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/slug/${slug}/unfavorite`;

      return new Promise(function(resolve, reject) {
        unirest.post(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {

          if (resp && resp.error) {
            reject(resp.error);
            return;
          }

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
      });
    }

    like(slug: string): Promise<any> {
      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/${slug}/like`;

      return new Promise(function(resolve, reject) {
        unirest.post(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {

          if (resp && resp.error) {
            reject(resp.error);
            return;
          }

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
      });
    }

    unlike(slug: string): Promise<any> {
      let authorization = `Bearer ${this.localStorageService.getItem('id_token')}`;
      let url = environment.apiAddress + `/api/v1/protected/clips/${slug}/unlike`;

      return new Promise(function(resolve, reject) {
        unirest.post(url)
        .headers({'Accept': 'application/json', 'Authorization': authorization})
        .send()
        .end(function(resp) {

          if (resp && resp.error) {
            reject(resp.error);
            return;
          }

          if (resp && resp.body) {
            if (typeof resp.body === 'string') {
              resolve(JSON.parse(resp.body));
            } else {
              resolve(resp.body);
            }
          } else {
            reject('no response');
          }
        });
      });
    }

    getTrendingInitial(period: string, language: string): Promise<TrendingClips> {
      return this.getTrending(undefined, period, language);
    }

    getTrending(cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
      let url = environment.apiAddress + `/api/v1/clips/trending?period=${period}&language=${language}`;

      if (cursor) {
        url += `&cursor=${cursor}`;
      }

      return this.restService.get(url);
    }

    getTrendingByChannel(channel: string, cursor: string|undefined, period: string): Promise<TrendingClips> {
      let url = environment.apiAddress + `/api/v1/clips/trending/channel/${channel}?period=${period}`;

      if (cursor) {
        url += `&cursor=${cursor}`;
      }

      return this.restService.get(url);
    }

    getTrendingByGame(game: string, cursor: string|undefined, period: string, language: string): Promise<TrendingClips> {
      let url = environment.apiAddress + `/api/v1/clips/trending/game/${game}?period=${period}&language=${language}`;

      if (cursor) {
        url += `&cursor=${cursor}`;
      }

      return this.restService.get(url);
    }
  }

  export default new ClipsService(new LocalStorageService(), new RestService());



